@import "../../../../styles/colors";

.wrapper {
  width: 100%;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
}
.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  display: flex;
  justify-content: center;
  align-items: center;

  .mobileMenu {
    padding: 5px;
    border-radius: 50%;
    transition: background-color, 1s;

    &:hover {
      background-color: $gray-hover-color;
    }

    @media (min-width: 910px) {
      display: none;
    }
  }

  img {
    width: 98px;

    @media (min-width: 910px) {
      width: 147px;
    }
  }
}

.userPanel {
  display: flex;
  align-items: center;
}

.searchDesktop {
  display: none;

  @media (min-width: 910px) {
    display: block;
    width: 300px;
    margin-right: 30px;
  }

  @media (min-width: 992px) {
    width: 400px;
    margin-right: 60px;
  }

  @media (min-width: 1200px) {
    width: 500px;
    margin-right: 100px;
  }
}

.basket {
  position: relative;
  cursor: pointer;
}

.basketText {
  display: none;

  @media (min-width: 576px) {
    margin-left: 10px;
    height: 31px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: 12px;
    font-weight: 600;
  }
}
